body {
    background-color: #C41230;
}

.App {
    margin: auto;
    padding: 15px 25px 15px 25px;
    background-color: white;
    width: 85%;
    box-shadow: 0px 0px 100px 20px rgba(0, 0, 0, 0.5);
    border-radius: 1ch;
}

h1, h3, h4 {
    font-family: sans-serif;
    font-weight: 700;
    color: #AD102A; 
    margin-bottom: 0px; 
    font-size: 36px
}

h3 {
    font-size: 28px;
}

h4 {
    font-size: 22px;
    margin-bottom: 10px;
}

p {
    font-size: 20px;
    margin-top: 10px;
}

li {
    font-size: 22px;
}

#author {
    font-size: 20px;
    font-weight: 500;
    font-family: sans-serif;
    margin: 5px 0px 32px 0px;
}

@media screen and (max-width: 900px) {
    .App {
        padding: 15px;
        width: 98%;
        box-shadow: 0px 0px 50px 2px rgba(0, 0, 0, 0.5);
    }
}