.row-container {
    display: flex;
    flex-direction: row;
    margin: 10px auto 40px auto;
    justify-content: space-between;
    max-width: 95%;
}

img {
    width: 90%;
    height: 90%;
}


@media screen and (max-width: 1200px) {
    .row-container {
        flex-direction: column;
    }
}