.quote-container {
    justify-content: center;
    margin: auto;
    width: auto;
    height: auto;
    display: flex;
    flex-direction: row;
    max-width: 75%;
}

.quotation-mark {
    height: 25px;
    width: 25px;
}

.quote {
    font-size: 22px;
    left: 0px;
    font-style: italic;
    font-weight: 500;
    white-space: pre-wrap;
    font-family: Georgia, 'Times New Roman', Times, serif;
}

.speaker {
    font-size: 18px;
    font-style: normal;
}